<!--  -->
<template>
  <div class="main">
    <el-container class="main_con">
      <el-aside width="23%" class="info_l">
        <div class="info">
          <div class="cover_out">
            <img class="pic" :src="playlistInfo.img700||'noimg.jpg'" @error="imgError($event)" />
          </div>
          <p class="info_title">歌单简介</p>
          <p class="info_text">{{playlistInfo.info}}</p>
        </div>
      </el-aside>
      <el-main class="info_r">

        <!-- 列表 -->
        <div>
          <div>
            <span class="title">{{playlistInfo.name}}</span>
            <p class="artist flex_c">
              <img class="user_img" :src="playlistInfo.uPic||'noimg.jpg'" @error="imgError($event)" />
              <span>{{playlistInfo.userName}}</span>
            </p>
            <p class="count">
              <i class="iconfont icon-icon_play_"></i>
              {{ playlistInfo.listencnt|playCnt }}
            </p>
            <!-- <span class="type">{{playlistInfo.tag}}</span> -->
          </div>
          <div class="btns">
            <el-button class="play" round>
              <i class="iconfont icon-icon_play_"></i>
              <span>播放全部</span>
            </el-button>
          </div>
          <!-- 列表组件 -->
          <div class="list_out">
            <music-table :pageNum="pageNum" :songList="playlistInfo.musicList" v-show="code==200"></music-table>
            <music-no-data v-show="code!=200 & code!=0"></music-no-data>
          </div>
          <pagination :code="code" :currentChange="getPlaylistInfo" :pageSize="30" :total="playlistInfo.total"></pagination>
        </div>
        <!-- 评论区 -->
        <div>
        </div>

      </el-main>
    </el-container>
  </div>
</template>

<script>
import { HttpManager } from "../../api";
import MusicTable from "../../components/MusicTable.vue";
import MusicNoData from "../../components/MusicNoData.vue";
import Pagination from '../../components/Pagination.vue';
export default {
  name: "PlaylistDetail",
  data () {
    return {
      status: false,
      code: 0,
      playlistInfo: {},
      pageNum: 0,
    };
  },

  components: {
    MusicTable,
    MusicNoData,
    Pagination,
  },

  methods: {
    getPlaylistInfo (val) {
      let that = this;
      this.pageNum = val;
      HttpManager.getPlaylistInfo({ pid: this.$route.params.pid, pn: val, rn: 30 })
        .then((res) => {
          that.code = parseInt(res.code);
          that.playlistInfo = res.data;
        })
        .catch((err) => { })
    },
    imgError (event) {
      event.currentTarget.src = "https://h5static.kuwo.cn/upload/image/4f768883f75b17a426c95b93692d98bec7d3ee9240f77f5ea68fc63870fdb050.png";
      // event.currentTarget.onerror = null;
    }
  },

  computed: {},
  filters: {
    playCnt (val) {
      let num = parseInt(val);
      if (num < 10000) {
        return num;
      } else {
        return (num / 10000).toFixed(1) + "万"; //取小数点后一位
      }
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getPlaylistInfo(1);
  },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang="scss" scoped>
@import "../../assets/css/font";
*,
:after,
:before {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
.main {
  margin-top: 20px;
  padding: 0 120px;
  display: block;
}
.main_con {
  min-width: 750px;
  height: 100%;
  font-size: 14px;
  line-height: 1;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
    sans-serif;
  .info_l {
    min-height: 300px;
    min-width: 135px;
    max-width: 250px;
    background: #fff;
    margin-right: 1%;
    width: 18%;
    // box-shadow: 0 0 30px 0 rgb(65 67 70 / 10%);
    .info {
      overflow: hidden;
      margin: 5px;
      box-shadow: 0 0 20px 0 rgb(65 67 70 / 10%);
      padding: 5px;
      .cover_out {
        max-height: 540px;
        overflow: auto;
        position: relative;
        width: 100%;
        .pic {
          width: 100%;
          height: 100%;
        }
      }
      .info_title {
        margin: 20px 0 10px;
        font-size: 20px;
        font-weight: 600;
        height: 30px;
        line-height: 30px;
      }
      .info_text {
        width: 100%;
        font-size: 13px;
        color: #888;
        line-height: 20px;
        margin-bottom: 20px;
      }
    }
  }
  .info_r {
    position: relative;
    width: 76.14%;
    z-index: 10;
    margin-left: 10px;
    padding: 0;
    .title {
      vertical-align: baseline;
      display: inline-block;
      margin: 20px 0 0;
      height: 30px;
      line-height: 30px;
      font-size: 28px;
      font-weight: 600;
    }
    .artist {
      font-size: 18px;
      height: 25px;
      color: #666;
      line-height: 25px;
      font-weight: 400;
      margin-top: 15px;
      .user_img {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        margin-right: 10px;
      }
    }
    .count {
      color: #999;
      margin-top: 15px;
      font-size: 16px;
      font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
        sans-serif;
    }
    .btns {
      margin: 25px 0 20px;
      .play {
        width: 130px;
        background: #31c27c;
        color: #fff;
        border: none;
        &:hover {
          color: #000;
        }
        &:active {
          box-shadow: 0 0 10px 0 rgb(65 67 70 / 30%) inset;
          border: none;
        }
      }
    }
    .list_out {
      box-shadow: 0 0 20px 0 rgb(65 67 70 / 8%);
    }
  }
}
.flex_c {
  display: flex;
  align-items: center;
}
</style>



